*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --colorWhite: #ffffff;
  --inpBackColor: #f5f8fa;
  --lightBlue: #0094ff;
  --lightGrey: #a2a2a2;
  --robotoBold: 'Roboto-Bold';
  --robotoRegular: 'Roboto-Regular';
}

body {
  display: flex;
  justify-content: center;
  background-color: #102250;
  padding-top: 48px;
}

.container {
  background-color: var(--colorWhite);
}

h1 {
  font-family: var(--robotoBold);
  padding-left: 63px;
  font-size: 28px;
}

form {
  margin-top: 24px;
}

.password div {
  display: flex;
}

svg {
  fill: var(--lightGrey);
}

.back-img-pass {
  background-color: var(--inpBackColor);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  margin: 11px 0px 11px 0px;
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  padding-left: 15px;
}

.username input,
.password input,
.filter input {
  outline: none;
  border: none;
  background-color: var(--inpBackColor);
  font-family: var(--robotoRegular);
}

.err-username,
.err-password,
.err-login {
  color: #e82828;
  font-family: var(--robotoRegular);
}

.login,
.logout,
.sort {
  outline: none;
  border: none;
  cursor: pointer;
  color: var(--colorWhite);
  font-family: var(--robotoRegular);
}

.login:enabled:hover,
.logout:hover,
.sort:hover {
  background-color: #00193685;
}

.login:disabled {
  background-color: var(--lightGrey);
}

.login:enabled,
.logout,
.sort {
  background-color: var(--lightBlue);
}

.control-panel {
  display: flex;
}

.users {
  display: flex;
  flex-direction: column;
}

.user-card {
  background-color: var(--colorWhite);
  width: 280px;
  border-radius: 7px;
  padding: 10px 0px 10px 10px;
  margin-bottom: 20px;
}

.logout,
.sort {
  font-size: 17px;
  border-radius: 30px;
  width: 100px;
}

.filter input {
  width: 250px;
  height: 30px;
  border-radius: 7px;
}


@font-face {
  font-family: 'Roboto-Bold';
  src: url(./fonts/Roboto-Bold.ttf);
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto-Regular';
  src: url(./fonts/Roboto-Regular.ttf);
  font-weight: normal;
  font-style: normal;
}

@media (max-device-width: 639px) {
  .container {
    width: 300px;
    border-radius: 10px;
    padding-top: 15px;
    padding-bottom: 55px;
  }

  form {
    padding-left: 10px;
  }

  .password {
    padding-bottom: 30px;
  }

  .password input {
    width: 250px;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
  }

  .username input {
    width: 280px;
    border-radius: 7px;
  }

  .password input,
  .username input {
    height: 50px;
    margin: 11px 0px 11px 0px;
    padding-left: 15px;
  }

  /* label {
    margin-right: 23px;
  } */

  .login {
    font-size: 17px;
    border-radius: 30px;
    width: 280px;
    height: 60px;
    padding: 11px 20px 9px 20px;
  }

  .control-panel {
    flex-direction: column;
  }

  .filter {
    padding-bottom: 30px;
  }

  .buttons {
    margin-bottom: 30px;
  }

  .sort {
    margin-right: 30px;
  }

  .filter input {
    height: 30px;
  }

  .logout,
  .sort {
    height: 30px;
  }
}

@media (min-device-width: 640px) {
  .container {
    width: 400px;
    border-radius: 10px;
    padding-top: 15px;
    padding-bottom: 55px;
  }

  form {
    padding-left: 30px;
  }

  .password input {
    height: 50px;
    width: 313px;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    margin: 11px 0px 11px 0px;
    padding-left: 15px;
  }

  .username input {
    width: 342px;
    padding-left: 15px;
    height: 50px;
    border-radius: 7px;
    margin: 11px 0px 11px 0px;
  }

  .login {
    font-size: 17px;
    border-radius: 30px;
    width: 342px;
    height: 60px;
    padding: 11px 20px 9px 20px;
  }

  .control-panel {
    flex-direction: row;
    padding-bottom: 48px;
  }

  .sort,
  .filter {
    margin-right: 48px;
  }

  .filter input {
    height: 50px;
  }

  .logout,
  .sort {
    height: 50px;
  }
}